
import { defineComponent, ref } from "vue";
import { dateToString } from "../mixins/formatDateMixin";
import { useRoute } from "vue-router";

export default defineComponent({
  props: ["missions", "completedMissions", "hero"],
  setup() {
    let nav = ref();
    const queryNav = useRoute().query.nav;
    nav.value = queryNav !== undefined ? queryNav : "missions";

    return { nav };
  },
  data() {
    return {
      firstLevelMaxExp: 10,
      levelIncreaseRate: 1.2,
    };
  },
  methods: {
    getLevelAndExp(): { level: number; exp: number; maxLevelExp: number } {
      let leftExpAfterMinus = this.hero.exp;
      let level = 1;
      let maxLevelExp = this.firstLevelMaxExp;

      while (leftExpAfterMinus - maxLevelExp >= 0) {
        // 9 - 10 = -1
        // 10 - 10 = 0
        // 11 - 10 = 1
        level++;
        leftExpAfterMinus = leftExpAfterMinus - maxLevelExp;
        maxLevelExp = maxLevelExp * this.levelIncreaseRate;
      }

      return {
        level,
        exp: Math.round(leftExpAfterMinus),
        maxLevelExp: Math.round(maxLevelExp),
      };
    },
    dateToString(value: string) {
      return dateToString(value);
    },
  },
});
