
import { defineComponent, ref, getCurrentInstance } from "vue";
import { hero, mission, onMission, completedMission } from "@/interface";
import { formatDate } from "./mixins/formatDateMixin";
import jsonToObject from "./mixins/forLoopMixin";
import fetchPost from "./mixins/fetchMixin";
import { CRUD } from "@/interface";
import router from "./router";
import {
  faArrowLeft,
  faEllipsis,
  faPenNib,
  faTrashCan,
  faPlus,
} from "@fortawesome/free-solid-svg-icons";
import { library } from "@fortawesome/fontawesome-svg-core";

library.add(faArrowLeft, faEllipsis, faPenNib, faTrashCan, faPlus);

export default defineComponent({
  setup() {
    const { proxy } = getCurrentInstance() as any;
    const proxyData = ref({
      url: proxy.$getUrl,
      fetchCode: proxy.$fetchCode,
    });
    let missions = ref<mission[]>([]);
    let completedMissions = ref<mission[]>([]);
    let hero = ref<hero>({ id: 1, exp: 0 });

    let fetchMissions = fetch(`${proxyData.value.url}&sheet=Missions`);
    let fetchCompletedMissions = fetch(
      `${proxyData.value.url}&sheet=CompletedMissions&tq=${encodeURIComponent(
        "select * order by A desc"
      )}`
    );
    let fetchHero = fetch(`${proxyData.value.url}&sheet=Hero`);

    Promise.all([fetchMissions, fetchCompletedMissions,fetchHero]).then((res) => {
      res[0].text().then((rep) => {
        missions.value = jsonToObject(rep);
        missions.value.forEach((mission: mission) => (mission.totalHour = 0));

        // res[0] he res[1] ä¸çŸ¥é“ä¸ºä»€ä¹ˆä¸èƒ½ä¸€èµ· runï¼Œ æ‰€ä»¥åªå¥½å†™åœ¨é‡Œé¢
        res[1].text().then((rep) => {
          completedMissions.value = jsonToObject(rep);

          completedMissions.value.forEach((cm: any) => {
            missions.value.find(
              (mission: mission) => mission.id === cm.missionId
            )!.totalHour += cm.minute / 60;
          });
        });
      });
   
      res[2].text().then((rep) => hero.value.exp = jsonToObject(rep)[0].exp)
    });

    return { missions, completedMissions, hero, proxyData };
  },
  data() {
    return {
      onMission: {} as mission,
      firstLevelMaxExp: 10,
      levelIncreaseRate: 1.2,
    };
  },
  methods: {
    createMission(mission: mission): void {
      mission.id = Math.max(...this.missions.map((m) => m.id)) + 1;
      fetchPost<mission>(
        CRUD.create,
        "Missions",
        this.proxyData.fetchCode,
        mission
      );
      this.missions.push(mission);
    },
    editMission(mission: mission): void {
      fetchPost<mission>(
        CRUD.update,
        "Missions",
        this.proxyData.fetchCode,
        mission
      );
      const index = this.missions.findIndex((m) => m.id === mission.id);
      this.missions[index] = mission;
    },
    removeMission(mission: mission): void {
      fetchPost<mission>(
        CRUD.delete,
        "Missions",
        this.proxyData.fetchCode,
        mission
      );
      const index = this.missions.findIndex((m) => m.id === mission.id);
      this.missions.splice(index, 1);
      router.push({ name: "home" });
    },
    startMission(onMission: onMission): void {
      fetchPost<onMission>(
        CRUD.update,
        "OnMission",
        this.proxyData.fetchCode,
        onMission
      );
    },
    completeMission(mission: mission): void {
      let completedMission = {} as completedMission;

      completedMission.id =
        this.completedMissions.length === 0
          ? 1
          : Math.max(...this.completedMissions.map((m) => m.id)) + 1;
      completedMission.missionId = mission.id;
      completedMission.title = mission.title;
      completedMission.minute = mission.minute;
      completedMission.point = mission.point;

      fetch(`${this.proxyData.url}&sheet=OnMission`)
        .then((res) => res.text())
        .then((rep) => {
          completedMission.completedDate =
            jsonToObject(rep)[0].onMissionCompletedDate;

          fetchPost<completedMission>(
            CRUD.create,
            "CompletedMissions",
            this.proxyData.fetchCode,
            completedMission
          );
          // this.completedMissions.unshift(completedMission);

          fetchPost<onMission>(
            CRUD.update,
            "OnMission",
            this.proxyData.fetchCode,
            {
              id: 1,
              onMissionCompletedDate: "",
              onMissionId: 0,
            }
          );

          this.hero.exp += mission.point;
          fetchPost<hero>(
            CRUD.update,
            "Hero",
            this.proxyData.fetchCode,
            this.hero
          );
        });
    },
  },
});
