import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import CreateMissionView from '../views/CreateMissionView.vue'
import EditMissionView from '../views/EditMissionView.vue'
import OnMissionView from '../views/OnMissionView.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/create-mission',
    name: 'createMission',
    component: CreateMissionView
  },
  {
    path: '/edit-mission/:missionId',
    name: 'editMission',
    component: EditMissionView
  },
  {
    path: '/on-mission/:missionId',
    name: 'onMission',
    component: OnMissionView
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
