
import { defineComponent, getCurrentInstance, ref } from "vue";
import { mission, onMission } from "@/interface";
import { useRoute } from "vue-router";
import jsonToObject from "../mixins/forLoopMixin";
import { formatDate } from "@/mixins/formatDateMixin";
import router from "@/router";

let countdownInterval: any;
let countdownTimeout: any;

export default defineComponent({
  props: ["missions"],
  emits: ["removeMission", "completeMission", "startMission"],
  setup(props) {
    const { proxy } = getCurrentInstance() as any;
    const proxyData = ref({
      url: proxy.$getUrl,
      fetchCode: proxy.$fetchCode,
    });

    const mission = ref<mission>();
    if (props.missions.length > 0) {
      const missionId = +useRoute().params.missionId;
      mission.value = props.missions.find((m: mission) => m.id === missionId);
    }
    return { mission, proxyData };
  },
  beforeCreate() {
    if (!this.mission) {
      const missionId = +useRoute().params.missionId;
      fetch(
        `${this.proxyData.url}&sheet=missions&tq=${encodeURIComponent(
          `select * where A = ${missionId}`
        )}`
      )
        .then((res) => res.text())
        .then((rep) => (this.mission = jsonToObject(rep)[0]));
    }
  },
  data() {
    return {
      mission: {} as mission,
      onMission: {} as onMission,
      isPlay: false,
      isCompleted: false,
      countdown: 0,
      second: 0,
    };
  },
  watch: {
    mission() {
      fetch(`${this.proxyData.url}&sheet=OnMission`)
        .then((res) => res.text())
        .then((rep) => {
          this.onMission = jsonToObject(rep)[0];
          if (this.onMission.onMissionId !== 0) {
            this.play(true);
          }
        });
    },
  },
  methods: {
    removeMission(mission: mission) {
      this.$emit("removeMission", { ...mission });
    },
    completeMission() {
      this.$emit("completeMission", { ...this.mission });
      router.push({ name: "home", query: { nav: "completed" } });
    },
    play(isOnMission = false) {
      this.isPlay = true;

      if (isOnMission === false) {
        this.onMission.id = 1;
        this.onMission.onMissionId = this.mission.id;
        const completedDate = new Date();
        completedDate.setSeconds(
          completedDate.getSeconds() + this.mission.minute * 60
        );
        this.onMission.onMissionCompletedDate = formatDate(completedDate);

        this.$emit("startMission", { ...this.onMission });

        this.countdown = this.second = this.mission.minute * 60;
        countdownInterval = setInterval(() => this.countdown--, 1000);
        countdownTimeout = setTimeout(() => {
          clearInterval(countdownInterval);
          this.isPlay = false;
          this.isCompleted = true;
          // this.$emit("completeMission", { ...this.mission });
          this.playSound();
        }, this.second * 1000 + 1000);
      } else {
        const completedDate = new Date(
          this.onMission.onMissionCompletedDate!.replace(" ", "T")
        );
        const dif = Math.floor(
          (completedDate.getTime() - new Date().getTime()) / 1000
        );
        if (dif > 0) {
          this.countdown = dif;
          this.second = this.mission.minute * 60;
          countdownInterval = setInterval(() => this.countdown--, 1000);
          countdownTimeout = setTimeout(() => {
            clearInterval(countdownInterval);
            this.isPlay = false;
            this.isCompleted = true;
            // this.$emit("completeMission", { ...this.mission });
            this.playSound();
          }, this.countdown * 1000 + 1000);
        } else {
          this.countdown = 0;
          this.second = this.mission.minute * 60;
          setTimeout(() => {
            this.isPlay = false;
            this.isCompleted = true;
            // this.$emit("completeMission", { ...this.mission });
          }, 2000);
        }
      }
    },
    stop() {
      clearInterval(countdownInterval);
      clearTimeout(countdownTimeout);
      this.isPlay = false;
    },
    getProgressPercent() {
      return `${(this.countdown / this.second) * 100}%`;
    },
    playSound() {
      const audioContext = new AudioContext();
      const element = document.getElementById("audio") as any;
      const source = audioContext.createMediaElementSource(element);
      source.connect(audioContext.destination);
      element.autoplay = true;
      element.play();
    },
  },
});
